import React, { Component } from 'react';
import './App.css';

import Main from './components/Main';

import { BrowserRouter, Route, Switch } from 'react-router-dom';
const http = require("http");

class App extends Component {
    
    constructor () {
        super();

        this.state = {
            data: null
        };
    }

    componentDidMount() {
        this.fetchData();
    }

    fetchData = () => {
        const url = "/projects/battleship";
        http.get(url, res => {
            res.setEncoding("utf8");
            let body = ""
            res.on("data", data => {
                body += data;
            });
            res.on("end", () => {
                // Store data to state
                this.setState({
                    data: body
                });
                console.log("BODY:"+body);
            });
        });
    };

    render(){
      return(
        //In case of more future pages & routes
        <div>
        <BrowserRouter basename={process.env.PUBLIC_URL}>
          <div className="sans-serif">
            <Switch>
              <Route exact path="/" component={Main}/>
            </Switch>
          </div>
        </BrowserRouter>

        </div>
      );
    }
}

export default App;
