//Subheader area with logos
class SubHeader extends React.Component{

  wrapLogos(){
    const logos = {
      'gitLogo': 'https://github.com/rintala', 
      'linkedinLogo': "https://www.linkedin.com/in/jonathan-rintala-57866a88",
    };

    return(
      <div className="logosWrapper">
      {
        Object.keys(logos).map((key,index) => 
          <div className="outsideLogoWrapper">
            <div className="insideLogoWrapper">
              <a href={logos[key]}>
                <div id={key}/>
              </a>
            </div>
          </div>
        )
      }
      </div>
    );
  }

  render(){
    return(
    <div style={{display:'flex', position: 'relative', marginBottom:'-18px'}}>
      <div className="App-logo"/>      
      {this.wrapLogos()}
    </div>
    );
  }
}

